import Searchkit from "searchkit";

import React from "react";
import ReactDOM from "react-dom";

//const host = "http://demo.searchkit.co/api/movies"
const host = "http://localhost:9200/tablets"
const sk = new Searchkit.SearchkitManager(host, {})
const Hits = Searchkit.Hits

class MovieHit extends React.Component {
  
  render() {
    const result = this.props.result;
        let url = "http://127.0.0.1:8000/tablets/" + result._source.slug
        return (
            <div className={this.props.bemBlocks.item().mix(this.props.bemBlocks.container("item")) + " col s12 m4"} key={result._id}>
                <a href={url} target="_blank">
                    <img className={this.props.bemBlocks.item("poster")} src={result._source.image} width="100" height="140"/>
                    <div className={this.props.bemBlocks.item("title")}>{result._source.name}</div>
                </a>
            </div>
        )
    }
  
}
const SearchkitProvider = Searchkit.SearchkitProvider;
const Searchbox = Searchkit.SearchBox;
class Application extends React.Component {
  render() {
   
    
    return (<div>
      
      <SearchkitProvider searchkit={sk}>
         <div className="search">
            <div className="search__query">
            <Searchbox searchOnChange={true} prefixQueryFields={["name^1"]} />
            </div>
            <div className="search__results">
            <Hits hitsPerPage={60} itemComponent={MovieHit}/>
            </div>
         </div>
      </SearchkitProvider>

    </div>);
  }
}


ReactDOM.render(
  <Application />,
  document.getElementById('app')
);




